  padding: 0;
  margin: 0;
}
 
body {
  font-family: sans-serif;
  background-color: white;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
}

#hamburgerButton {
  display: inline-block;
	min-width: 44px;
  min-height: 44px;
	text-decoration: none;
	color: white !important
}
/*
  AppBar
*/
 
.app-bar {
  padding: 8px 16px;
  background-color: #222831;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}
 
.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}
 
.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__brand h1 {
  color: white;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}
 
.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: #222831;
  overflow: hidden;
}
 
.app-bar .app-bar__navigation.open {
  left: 0;
}
 
.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: white;
  font-size: 17px;
  padding: 8px;
  width: 100%;
  transition: all 0.2s ease-in-out;
  min-width: 44px;
  min-height: 44px;
}

.app-bar .app-bar__navigation ul li:hover a {
  border: 3px solid #272d35;
  background-color: #272d35;
  color: white;
}

.skip-to-content {
  position: absolute;
  top: -9999px;
  left: -9999px;
}
 
.skip-to-content:focus {
  position: fixed;
  top: 10px;
  left: 10px;
  background-color: #fff;
  padding: 5px 10px;
  z-index: 9999;
}
/*
  Hero
*/
.jumbotron {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 400px;
}

.jumbotron img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}


/*
  Main Content
*/
 
main {
  padding: 16px;
  flex: 1;
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}
 
.content {
  margin: 0 auto;
  min-height: 100%;
}
 
.content .content__heading {
  font-weight: bold;
  text-align: center;
}
 
 
/*
  restaurants
*/
 
.restaurants {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin: 32px 0;
}
 
 
/*
  restaurant Item
*/
 
.restaurant-item {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
}
 
.restaurant-item__header {
  position: relative;
}
 
.restaurant-item .restaurant-item__header #restaurant-item__header__poster {
  width: 100%;
  height: 250px;
}
 
.restaurant-item .restaurant-item__header .restaurant-item__header__city {
  position: absolute;
  top: 5px;
  background-color: #bdee5a;
  padding: 8px 16px;
  border-radius: 0px 7px 7px 0px;
  font-weight: bold;
  text-transform: uppercase;
}
 
.restaurant-item .restaurant-item__content {
  padding: 16px;
  text-align: justify;
}
 
.restaurant-item .restaurant-item__content h3 {
  margin: 0 0 10px 0;
}
 
.restaurant-item .restaurant-item__content h3 a {
	display: inline-block;
  color: #222831;
  text-decoration: none;
  min-width: 44px;
  min-height: 44px;
}

.restaurant-item .restaurant-item__content h2 {
  font-size: 13px;
  margin-bottom: 3px;
}
 
.restaurant-item .restaurant-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}
 
 
/*
  restaurant
*/
 
.restaurant {
  margin: 0 auto;
  max-width: 800px;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
 
.restaurant .restaurant__poster {
  max-width: 100%;
  height: auto;
}

.restaurant .restaurant__name {
  margin-bottom: 1px;
}
 
.restaurant h3, p {
  margin: 5px 0;
}

.restaurant p {
  text-align: justify;
}

 /*
  Like
*/
.like {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 24px;
  color: white;
  cursor: pointer;
}

.restaurant-item__not__found {
  text-align: center;
  font-weight: bold;
}
 
/*
  Footer
*/
 
/*
  Footer
*/
 
footer {
  padding: 16px;
  background-color: #222831;
}

footer p {
  color: white;
  text-align: center;
  font-size: 18px;
